.elem-input {
    border: 1px solid $color-gray-light;
    border-radius: 12px;
    font-size: 16px;
    line-height: 24px;
    padding: 10px 16px;
    height: 52px;
    outline: none;
    transition: all 0.3s ease-in-out;
    width: 100%;
    background-position: calc(100% - 16px) center;

    @media (max-width: $lg) {
        font-size: 13px;
        line-height: 19px;
        letter-spacing: 0px;
        height: 44px;
        border-radius: 8px;
    }

    @media (max-width: $sm) {
        font-size: 12px;
        line-height: 17px;
        letter-spacing: -0.2px;
        height: 44px;
        border-radius: 8px;
    }

    &::placeholder {
        color: $color-gray;
    }

    &:focus {
        border-color: $color-gray;
    }

    &__icon {
        position: absolute;
        left: 12px;
        top: 50%;
        transform: translate(0, -50%);
        display: flex;
        z-index: 11;
    }

    &__icon + & {
        padding-left: 44px;
    }

    &--md {
        height: 40px;
        font-size: 14px;
        line-height: 21px;
        letter-spacing: 0;
        border-radius: 20px;
    }

    &--sm {
        font-size: 12px;
        line-height: 14px;
        border-radius: 16px;
        padding: 4px 16px;
        height: 32px;
    }

    &--xs {
        font-size: 11px;
        line-height: 14px;
        height: 28px;
        padding: 7px 8px;
        border-radius: 8px;

        @media (max-width: $lg) {
            font-size: 13px;
            line-height: 19px;
            height: 44px;
            border-radius: 10px;
            padding: 7px 16px;
        }
    }

    &--price {
        width: 100px;
        text-align: center;

        @media (max-width: $lg) {
            font-size: 10px;
            line-height: 12px;
            height: 32px;
            width: 80px;
        }
    }

    &--price-label {
        padding: 10px 16px 10px 36px;

        @media (max-width: $lg) {
            padding-left: 28px;
        }

        @media (max-width: $md) {
            padding-left: 50px;
        }

        &:focus {
            border-color: $color-smoke;
        }

    }

    &--gray {
        background-color: $color-smoke;
        border-color: $color-smoke;
    }

    &--white {
        background-color: $color-white;
    }

    &--city {
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0)'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M20.6262 3.92252C20.7571 4.0793 20.7795 4.29989 20.6828 4.47975L11.9403 20.7368C11.8285 20.9447 11.5865 21.0461 11.3599 20.98C11.1333 20.9139 10.9838 20.6983 11.0013 20.4629L11.6714 11.4483L2.44866 10.4974C2.2136 10.4731 2.02761 10.2876 2.00272 10.0526C1.97783 9.81766 2.12085 9.59733 2.34562 9.5244L20.0881 3.76735C20.2823 3.70432 20.4954 3.76575 20.6262 3.92252ZM4.90488 9.7453L12.2574 10.5034C12.5265 10.5311 12.7248 10.768 12.7047 11.0378L12.1728 18.1931L19.2122 5.10286L4.90488 9.7453Z' fill='%23101010'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0'%3E%3Crect width='24' height='24' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
        background-repeat: no-repeat;
        background-position: 12px center;

        padding-left: 40px;
        height: 32px;
        font-size: 12px;
        line-height: 14px;

        @media (max-width: $md) {
            border-radius: 16px;
        }
    }

    &--status {
        position: absolute;
        font-size: 14px;
        line-height: 16px;
        color: $color-gray;
        left: 55px;
        height: calc(100% - 20px);
        top: 20px;
        display: flex;
        align-items: center;

        @media (max-width: $lg) {
            font-size: 13px;
            line-height: 19px;
        }

        @media (max-width: $md) {
            top: 21px;
            height: calc(100% - 21px);
            font-size: 12px;
            line-height: 17px;
        }
    }

    &--textarea {
        resize: none;
        height: 184px;
        padding: 28px 31px;
        letter-spacing: -.2px;
        display: block;

        @media (max-width: $lg) {
            height: 129px;
            padding: 17px 19px;
        }

        @media (max-width: $md) {
            padding: 8px 9px;
        }
    }

    &.succsess {
        border-color: $color-green;

        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='12' cy='12' r='12' fill='%234EF25E'/%3E%3Cpath d='M6 11.75L10.5 16.25L18 8.25' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
        background-repeat: no-repeat;
    }
}
