*,
*::before,
*::after {
	box-sizing: border-box;
}

body {
	background: $bg-base;
	font-family: $font-base;
    font-size: $font-size-base;
    font-weight: $font-weight-base;
	line-height: $line-height-base;
    color: $color-base;
    letter-spacing: -0.2px;
    width: 100%;
    margin: 0;
    padding: 0;

    @media (max-width: $lg) {
        font-size: 13px;
        line-height: 19px;
    }

    @media (max-width: $md) {
        font-size: 12px;
        line-height: 17px;
    }

    &.scroll {
        overflow: hidden;
        height: 100vh;

        position: fixed;
        cursor: pointer;
    }
}

.page {
    min-width: 320px;
    overflow: hidden;
    width: 100%;
    position: relative;

    &:after {
        content: '';
        position: fixed;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background: rgba($color-base, 0.5);
        opacity: 0;
        transition: all 0.3s ease-in-out;
        visibility: hidden;
    }

    &.overflow {
        &:after {
            opacity: 1;
            visibility: visible;
        }
    }
}

.container {
    @media (max-width: $lg) {
        padding-left: 16px;
        padding-right: 16px;
    }

    @media (max-width: $md) {
        padding-left: 15px;
        padding-right: 15px;
    }

    &__inner {
        padding: 0 36px;

        @media (max-width: $lg) {
            padding-left: 30px;
            padding-right: 30px;
        }

        @media (max-width: $md) {
            padding-left: 0;
            padding-right: 0;
        }
    }
}

h1, h2, h3, h4, h5, h6 {
	font-weight: normal;
	margin: 0;
}

a {
	color: inherit;
    transition: all .3s ease-in-out;

	&:hover {
        text-decoration: none;
        color: $color-red;
	}
}

p {
    .content & {
        margin-bottom: 22px;

        @media (max-width: $md) {
            margin-bottom: 16px;
        }
    }
}

ul, ol {
	list-style: none;
	padding: 0;
    margin: 0;
    position: relative;

	& li {
		position: relative;
		padding: 0;
		margin: 0;
	}
}

ol:not([class]) {
    counter-reset: item;
    margin-bottom: 25px;

    @media (max-width: $lg) {
        margin-bottom: 20px;
    }

    &:last-child {
		margin-bottom: 0;
    }

    & li {
        padding-left: 0;

        @media (max-width: $lg) {
            letter-spacing: 0;
        }

        @media (max-width: $md) {
            font-size: 12px;
            line-height: 17px;
            letter-spacing: -0.2px;
        }

        & + li {
            margin-top: 25px;

            @media (max-width: $lg) {
                margin-top: 18px;
            }

            @media (max-width: $md) {
                margin-top: 17px;
            }
        }

        &:before {
            content: counters(item,".") ". ";
            counter-increment: item;
            font-weight: 800;

            @media (max-width: $md) {
                position: relative;
                left: auto;
                top: auto;
            }

            .content & {
                @media (max-width: $lg) {
                    font-size: 14px;
                    line-height: 21px;
                }

                @media (max-width: $md) {
                    font-size: 12px;
                    line-height: 17px;
                }
            }
        }
    }

    & > li {
        &:before {
            .content & {
                @media (max-width: $md) {
                    position: absolute;
                    left: 16px;
                    top: 14px;
                }
            }
        }

        & ol li:before {
            .content & {
                @media (max-width: $md) {
                    position: relative;
                    left: auto;
                    top: auto;
                }
            }
        }
    }

    & ol {
        margin-top: 18px;

        @media (max-width: $lg) {
            margin-top: 17px;
        }

        @media (max-width: $md) {
            margin-top: 13px;
        }
    }

    & p {
        &:first-child {
            display: inline;
        }
    }
}

ul:not([class]) {
    margin-bottom: 25px;
    counter-reset: item2;

    @media (max-width: $lg) {
        margin-bottom: 20px;
    }

    &:last-child {
		margin-bottom: 0;
    }

    & li {
        padding-left: 0;

        & + li {
            margin-top: 0;
        }

        &:before {
            content: '';
            counter-increment: item2;
            font-weight: 400;
            margin-right: 4px;
        }
    }
}

.ul-title {
    @media (max-width: $lg) {
        font-size: 14px;
        line-height: 21px;
    }

    @media (max-width: $md) {
        display: block;
        border: 1px solid $color-base;
        border-radius: 22px;
        font-size: 12px;
        line-height: 15px;
        padding: 14px 27px 13px;
    }

    & + ol {
        @media (max-width: $md) {
            display: none;
        }
    }

    .open & + ol {
        @media (max-width: $md) {
            display: block;
        }
    }

    &:after {
        @media (max-width: $md) {
            content: '';
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.64645 8.14645C4.45118 8.34171 4.45118 8.65829 4.64645 8.85355L11.6464 15.8536C11.7402 15.9473 11.8674 16 12 16C12.1326 16 12.2598 15.9473 12.3536 15.8536L19.3536 8.85355C19.5488 8.65829 19.5488 8.34171 19.3536 8.14645C19.1583 7.95118 18.8417 7.95118 18.6464 8.14645L12 14.7929L5.35355 8.14645C5.15829 7.95118 4.84171 7.95118 4.64645 8.14645Z' fill='%23101010'/%3E%3C/svg%3E%0A");
            height: 24px;
            width: 24px;
            position: absolute;
            right: 16px;
            top: 10px;
            transition: all .3s ease-in-out;
        }

        .open & {
            transform: rotate(180deg);
        }
    }
}

.ul-attention {
    display: inline-block;
    margin-top: 23px;

    @media (max-width: $md) {
        margin-top: 17px;
    }
}

.list-mark {
    margin-bottom: 32px;

    &:last-child {
        margin-bottom: 0;
    }

    & li {
        .content & {
            margin-bottom: 25px;

            @media (max-width: $lg) {
                margin-bottom: 22px;
            }

            @media (max-width: $md) {
                margin-bottom: 17px;
            }
        }

        &:before {
            content: '';
            margin-right: 12px;

            @media (max-width: $lg) {
                margin-right: 7px;
            }
        }
    }
}

img,
svg {
    max-width: 100%;
}

svg {
    max-height: 100%;
}

table {
	border-collapse: collapse;
	table-layout: fixed;
    width: 100%;
}

