}

%btn-arrow {
    background: transparent;
    border: 0;
    box-sizing: border-box;
    height: 32px;
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;

    @media (max-width: $lg) {
        height: 26px;
        width: 26px;
    }

    @media (max-width: $md) {
        height: 22px;
        width: 22px;
    }

    &:after {
        content: '';
        @extend %arrow;
    }

    &:hover {
        &:after {
            border-color: $color-red;
        }
    }
}

%input-hide {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 16px;
    opacity: 0;
}

@media (min-width: $xl + 1) {
    .hidden-xl {
        display: none !important;
    }

    .text-xl-left {
        text-align: left !important;
    }

    .text-xl-right {
        text-align: right !important;
    }
}

@media (min-width: $lg + 1) and (max-width: $xl) {
    .hidden-lg {
        display: none !important;
    }

    .text-lg-left {
        text-align: left !important;
    }

    .text-lg-right {
        text-align: right !important;
    }
}

@media (min-width: $md + 1) and (max-width: $lg) {
    .hidden-md {
        display: none !important;
    }

    .text-md-left {
        text-align: left !important;
    }

    .text-md-right {
        text-align: right !important;
    }
}

@media (min-width: $sm + 1) and (max-width: $md) {
    .hidden-sm {
        display: none !important;
    }

    .text-sm-left {
        text-align: left !important;
    }

    .text-sm-right {
        text-align: right !important;
    }
}

@media (max-width: $sm) {
    .hidden-xs {
        display: none !important;
    }

    .text-xs-left {
        text-align: left !important;
    }

    .text-xs-right {
        text-align: right !important;
    }
}
